import Panel from '../../components/panel/Panel';
import Overview from '../../components/overview/Overview';
import Carbon from '../../components/carbon/Carbon';
import Electricity from '../../components/electricity/Electricity';
import RenewableEnergy from '../../components/renewable-energy/RenewableEnergy';
import UnitElectricity from '../../components/unit-electricity/UnitElectricity';
import Waste from '../../components/waste/Waste';
import Water from '../../components/water/Water';
import Tag from '../../components/tag/Tag';

export default function HomePage() {
  return (
    <div className="grid grid-rows-3 grid-cols-3 p-4 pt-20 -mt-16 gap-4 h-screen w-screen overflow-hidden">
      <Panel
        className="row-span-1 col-span-3"
        title=" Overview"
        to="/overview"
        subtitle={<Tag>2021.01 - 06</Tag>}>
        <Overview />
      </Panel>
      <Panel className="row-span-1 col-span-1" title="" to="/carbon">
        <Carbon />
      </Panel>
      <Panel className="row-span-1 col-span-1" title="" to="/renewable-energy">
        <RenewableEnergy />
      </Panel>
      <Panel className="row-span-1 col-span-1" title="" to="/electricity">
        <Electricity />
      </Panel>
      <Panel className="row-span-1 col-span-1" title="" to="/waste">
        <Waste />
      </Panel>
      <Panel className="row-span-1 col-span-1" title="" to="/water">
        <Water />
      </Panel>
      <Panel className="row-span-1 col-span-1" title="" to="/unit-electricity">
        <UnitElectricity />
      </Panel>
    </div>
  );
}
